import request from '../utils/request'
import qs from 'qs';

export default {
    //
    getConfigs : (taskid)=>{
        return request({
            url: 'TaskConfig/' + taskid,
            method: 'get'
            //params: query
        });
    },
    //
    addConfig: (params) =>{
        return request({
            url: 'TaskConfig',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
              },
            method: 'post',
            data: qs.stringify(params)
        })
    },
    //
    delConfig: (tcid)=>{
        return  request({
            url: 'TaskConfig',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
              },
            method: 'delete',
            data:qs.stringify({'tcid': tcid})
        })
    }
}