import request from '../utils/request'
import qs from 'qs';

export default {
    //
    getCommands : (taskid)=>{
        return request({
            url: 'TaskCommand/' + taskid,
            method: 'get'
            //params: query
        });
    },
    //
    addCommand: (params) =>{
        return request({
            url: 'TaskCommand/Add',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
              },
            method: 'post',
            data: qs.stringify(params)
        })
    },
    //
    sendCommand: (taskId, tcid)=>{
        return request({
            url: 'TaskCommand/Command',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
              },
            method: 'post',
            data:qs.stringify({
                'taskId': taskId,
                'tcid': tcid
            })
        })
    },
    //
    delCommand: (tcid)=>{
        return  request({
            url: 'TaskCommand',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
              },
            method: 'delete',
            data:qs.stringify({'tcid': tcid})
        })
    }
}